/** @jsx jsx */
import { jsx, css } from "@emotion/react"
import ProductPrice from "../price"
import ProductPriceSaleNotice from "../sale-notice"
import { firstPriceCompareAt } from "Utils"

function ProductPricesCompareAt({
  prices,
  compareAt,
  selectedVariant,
  showPriceRange,
  settings,
}) {
  const ProductPricesCompareAtWrapperCSS = css`
    display: flex;
    align-items: center;
    margin: ${selectedVariant ? "6px 0 0 0" : "10px 0 0 0"};

    .wps-product-individual-price {
      font-size: 15px;
    }

    &:empty {
      display: none;
    }
  `

  if (selectedVariant) {
    if (selectedVariant.node.compareAtPriceV2) {
      var hasCompareAtPrice = selectedVariant.node.compareAtPriceV2.amount
    } else {
      var hasCompareAtPrice = false
    }
  } else {
    var hasCompareAtPrice = firstPriceCompareAt(prices)
  }

  return (
    <>
      <ProductPrice
        selectedVariant={selectedVariant}
        compareAt={false}
        prices={prices}
        showPriceRange={showPriceRange}
        settings={settings}
      />

      {compareAt && hasCompareAtPrice ? (
        <div
          className="wps-product-prices-compare-at"
          css={ProductPricesCompareAtWrapperCSS}
        >
          <ProductPriceSaleNotice>
            <ProductPrice
              selectedVariant={selectedVariant}
              compareAt={true}
              prices={prices}
              showPriceRange={showPriceRange}
              settings={settings}
            />
          </ProductPriceSaleNotice>
        </div>
      ) : null}
    </>
  )
}

export default wp.element.memo(ProductPricesCompareAt)
